<template>
    <div class="">
      <div class="bindWeixin" v-if="wxApplicationList.length==0">
        <p v-if="type==1" style="font-size:14px;"></p>
        <p v-if="type==2"></p>
        <div class="bwText">
           <p>{{type==1?'':''}}</p>
           <a-button type="primary" @click="addWeixinApplication" size="large">{{type==1?'':''}}</a-button>
           <span v-if="type==1"></span>
        </div>
      </div>
       <!--  -->
      <a-modal
        :title="type==1?'':''"
        :visible="addWxModal"
        @ok="handleAddOk"
        @cancel="handleAddCancel"
        okText=""
        cancelText="">
        <div class="addAppltion" v-if="type==1">
          <p></p>
          <div><br />
              <br />
              <br />
              <br />
              <br />
              
          </div>
        </div>
        <div class="addAppltion"  v-if="type==2">
          <p></p>
          <div><br />
              <br />
              <br />
              <br />
              <br />
          </div>
        </div>
      </a-modal>
       <!--  -->

      <div v-html="html">
         <!-- {{html}} -->
      </div>

      <div v-if="wxApplicationList.length>0 "  class="wxApplication">
        <div class="wxAddBtn" ><a-button type="primary" @click="addWeixinApplication">{{type==1?'':''}}</a-button></div>
        <div class="bindWxList">
          <a-menu mode="vertical" class="wxMenu"  v-model="activeKey" @click='changeMenu'>
            <a-menu-item  v-for="(item,index)  in wxApplicationList"  :key="index">
              {{item.nickName}}
            </a-menu-item>
          </a-menu>
          <div class="wxMenuCon" >
            {{type==1?'':''}}{{wxApplicationList[currentKey].nickName}}<br />
            {{wxApplicationList[currentKey].inputTime}}<br />
             <a-button type="primary" @click="relieveBind"></a-button>
          </div>
        </div>
      </div>
     <!--  -->
      <a-modal
        title=""
       :visible="relieveBindModal"
        okText=""
        cancelText=""
        @ok="handleEelieveOk"
        @cancel="handleRelieveCancel"
       >
        <p>{{type==1?' ':' '}}</p>
       </a-modal>
    </div>
</template>
<script>
export default {
    name: "bindWeixin",
    components: {},
    props:['type'],
    data() {
      return {
        addWxModal:false,
        relieveBindModal:false,
        html:"",
        activeKey:[0],
        currentKey:0,
        wxApplicationList:[]
      }
    },
    created(){
      this.getList()
    },
    mounted(){},
    methods: {
      getList(){
        this.$axios.get("/session/channel/wx/listJson",{type:this.type}).then((res) => {
          console.log(res,'',this.type);
          this.wxApplicationList = res.list
        })
      },
      addWeixinApplication(){
        this.addWxModal = true
      },
      handleAddOk(){  // 
        let type = this.type
        this.$axios.get("/open/bind",{type:this.type}).then((res) => {
          console.log(res.url,'',this.type);
          let nowurl = res.url
          document.location = nowurl
          // if(type==1){
          //   this.html = `<iframe class="qfy-iframe" width="100%" height="500px" frameborder="0" src="${nowurl}"></iframe>`;
          // }else if(type==2){
            // this.html = `<iframe class="qfy-iframe" width="100%" height="500px" frameborder="0" src="${nowurl}"></iframe>`;
          // }
          this.addWxModal = false
        })
      },
      handleAddCancel(){
        this.addWxModal = false
      },
     // 
      relieveBind(){
        this.relieveBindModal = true
      },
      handleEelieveOk(){

      },
      handleRelieveCancel(){
        this.relieveBindModal = false
      },
      changeMenu(item){
        this.currentKey = item.key
      }
    }
}
</script>

<style lang="less" scoped>
.bindWeixin{
  padding:0 10px;
  .bwText{
    margin-top: 14em;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center ;
    p{
      font-size:24px;
      color:#0d1a26;
      line-height:32px;
    }
    span{
      margin-top:2em;
    }
  }
}
.addAppltion{
  p{
    font-size:14px;
    color:#0d1a26;
  }
  div{
    line-height:25px;
  }
}
.bindWxList{
  width:100%;
  display:flex;

}
.wxApplication{
  margin:0 30px;
  .wxAddBtn{
    text-align:right;margin-bottom:10px;
  }
  .wxMenu{
    width: 256px;margin-right:10px;border:1px solid #ddd;
  }
  .wxMenuCon{
    padding:20px;border:1px solid #ddd;flex:1;line-height:30px;
  }
}
</style>



